body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%;min-height:100%}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%;min-height:100%}.App-header{background-color:#4b5d67;height:10vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:20px;color:white}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%;min-height:100%}.form{margin:30px 0;display:flex;width:100%;height:40px;justify-content:space-between;align-items:center}.form fieldset{width:40%;height:100%}.form fieldset:last-of-type{position:relative}.form input[placeholder="http://"]{width:80%;font-size:1.1em}.form ::-webkit-input-placeholder{color:#d6e0f0}.form :-ms-input-placeholder{color:#d6e0f0}.form ::-ms-input-placeholder{color:#d6e0f0}.form ::placeholder{color:#d6e0f0}.form input[type="radio"]{opacity:0;position:absolute;border-radius:0;bottom:10px;height:28px;width:60px;margin:0 20px}.form input[type="radio"]+label{margin:0 20px;padding:5px;width:100px;border-radius:3px}.form input[type="radio"]:hover+label{cursor:pointer}.form input[type="radio"]:checked+label{background-color:#cdc9c3;color:#4b5d67;font-weight:700}.form button{height:90%;align-self:center;width:100px;background:#d9e4dd;color:#4b5d67;border:none}.form+div{height:50px;width:100%;display:flex;justify-content:center;align-items:center;border:1px solid black}.form+div span{margin:5px}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%;min-height:100%}#results{margin-bottom:10vh;max-height:62vh;overflow:scroll;border:1px solid #cdc9c3;padding:10px}

*{font-family:"Josefin Sans",sans-serif}main{margin:0 5% 0 5%;min-height:100%}.App-footer{background-color:#cdc9c3;height:10vh;width:100%;bottom:0;color:#4b5d67;display:flex;align-items:center;justify-content:center;position:absolute}.App-footer p{text-align:left;width:100%;padding-left:50px;font-weight:700;font-size:12px}

